

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: sans-serif;
}

section{
    width: 100%;
    height: 65vh;
    background-size: cover;
    background-position: center;
}

section .main{
    display: flex;
    align-items: center;
    justify-content: space-around;
    position: relative;
    top: 10%;
}

section .main h1{
    position: relative;
    font-size: 48px;
    top: 80px;
    left: 25px;
}

section {
    background-color: #FAF2D8;

}

section .main .main_tag .main_btn{
    background: #089da1;
    padding: 10px 20px;
    position: relative;
    top: 150px;
    left: 25px;
    text-decoration: none;
    color: #fff;
}

section .main .main_img img{
    width: 650px;
    position: relative;
    top: 55px;
    left: 20px;
}

.categories {
    width: 100%;
    height: auto;
    margin: 35px 0;
}

.txt_link {
    text-decoration: none;
    color: white;
}
